import { createFeatureSelector, createSelector, createReducer, Action, on } from '@ngrx/store';

import * as fromRoot from '../reducers';

export const featureKey = 'directory';

export interface DirectoryState {

}

export interface State extends fromRoot.State {
    directory: DirectoryState;
}

export const initialState: DirectoryState = {

};

const directoryReducer = createReducer(initialState);

export function reducer(state: DirectoryState | undefined, action: Action) {
    return directoryReducer(state, action);
}

export const selectDirectory = createFeatureSelector<State, DirectoryState>(featureKey);
