import { Component, OnInit, ViewEncapsulation, OnDestroy, EventEmitter, Inject } from '@angular/core';
import { Subject, zip, Observable } from 'rxjs';
import {
    distinct,
    filter,
    takeUntil,
    debounceTime,
    withLatestFrom,
    skip
} from 'rxjs/operators';
import { Store, select } from '@ngrx/store';
import * as fromRoot from '../../reducers';
import { ChartNode, ChartMetaInfo, NodeInformation, PreloadedData } from 'src/app/models';
import { PageChangedEvent } from 'ngx-bootstrap/pagination/public_api';
import * as Resources from '../../strings';
import { L10N_LOCALE, L10nLocale } from 'angular-l10n';
import { DirectoryService } from '../directory.service';
import { DirectoryRequest } from '../directory.models';
import { DialogShow } from 'src/app/shared/shared.actions';
import { SharedModals } from 'src/app/shared/shared.models';
import { InfoOpen } from 'src/app/chart/chart.actions';
import * as fromChart from '../../chart/chart.reducers';
import { AnalyticsService } from 'src/app/core/services/analytics.service';

const REQUEST_PAGE_SIZE = 15;
const DISPLAY_PAGE_SIZE = 9;

@Component({
    selector: 'toc-directory',
    templateUrl: './directory.component.html',
    encapsulation: ViewEncapsulation.None,
    styleUrls: ['./directory.component.scss']
})
export class DirectoryComponent implements OnInit, OnDestroy {
    useInfinityScrollForEverything = true;
    Resources = Resources;
    displayPageSize = DISPLAY_PAGE_SIZE;
    users: ChartNode[] = [];
    searchResults: ChartNode[] = [];
    searchPage: ChartNode[] = [];
    allUsers: ChartNode[] = [];
    nextPageUrl = '';
    nextPageUrl$ = new Subject<string>();
    scrollDown$ = new Subject();
    chartId: string;
    chartMeta$: Observable<ChartMetaInfo>;
    preloadedData$: Observable<PreloadedData>;
    isFetching$: Observable<boolean>;
    preloadedData: PreloadedData = null;
    isPaginationNeeded = false;
    searchString = '';
    searchStringEmitter = new EventEmitter<string>();
    optionsLimit: 10;
    hasFullData = false;
    isLoading = true;
    nodeInformation$: Observable<NodeInformation>;
    private unsubscribe: Subject<void> = new Subject();

    constructor(
        @Inject(L10N_LOCALE) public locale: L10nLocale,
        private directoryService: DirectoryService,
        private store: Store<fromRoot.State>,
        private analytics: AnalyticsService
    ) {
        store.pipe(select(fromRoot.getChartId), takeUntil(this.unsubscribe)).subscribe(x => {
            this.chartId = x;
            this.analytics.send('Directory', { chartId: x });
        });
        this.chartMeta$ = store.pipe(select(fromRoot.getChartMeta));
        this.preloadedData$ = store.pipe(select(fromRoot.getPreloadedData));
        this.preloadedData$.pipe(takeUntil(this.unsubscribe)).subscribe(x => (this.preloadedData = x));
    }

    ngOnInit() {
        this.searchStringEmitter
            .pipe(debounceTime(200), takeUntil(this.unsubscribe), withLatestFrom(this.chartMeta$))
            .subscribe(([searchString, chartMeta]) => {
                this.searchString = searchString;
                if (searchString.length > 2 || (this.hasFullData && searchString.length > 0)) {
                    if (this.hasFullData) {
                        const lowerSearchString = searchString.toLowerCase();
                        this.searchResults = this.allUsers.filter(x => {
                            return (
                                x.DisplayName.toLowerCase().indexOf(lowerSearchString) !== -1 ||
                                (x.Email && x.Email.toLowerCase().indexOf(lowerSearchString) !== -1) ||
                                (x.JobTitle && x.JobTitle.toLowerCase().indexOf(lowerSearchString) !== -1) ||
                                (x.TOC_Fields[chartMeta.EmailColumn] &&
                                    x.TOC_Fields[chartMeta.EmailColumn].toLowerCase().indexOf(lowerSearchString) !==
                                        -1) ||
                                (x.TOC_Fields[chartMeta.JobTitleColumn] &&
                                    x.TOC_Fields[chartMeta.JobTitleColumn].toLowerCase().indexOf(lowerSearchString) !==
                                        -1) ||
                                (x.TOC_Fields[chartMeta.GroupColumn] &&
                                    x.TOC_Fields[chartMeta.GroupColumn].toLowerCase().indexOf(lowerSearchString) !==
                                        -1) ||
                                (x.TOC_Fields[chartMeta.PhoneColumn] &&
                                    x.TOC_Fields[chartMeta.PhoneColumn].toLowerCase().indexOf(lowerSearchString) !==
                                        -1) ||
                                (x.TOC_Fields[chartMeta.NameColumn] &&
                                    x.TOC_Fields[chartMeta.NameColumn].toLowerCase().indexOf(lowerSearchString) !== -1)
                            );
                        });
                        this.searchPage = this.searchResults.slice(0, DISPLAY_PAGE_SIZE);
                    } else {
                        const request: DirectoryRequest = {
                            nextPageUrl: '',
                            pageSize: REQUEST_PAGE_SIZE,
                            chartId: this.chartId,
                            searchString
                        };
                        if (this.searchResults.length === 0) {
                            this.isLoading = true;
                        }
                        this.directoryService
                            .getDirectory(request)
                            .pipe(takeUntil(this.unsubscribe))
                            .subscribe(response => {
                                this.searchResults = response.users;
                                this.searchPage = this.searchResults.slice(0, DISPLAY_PAGE_SIZE);
                                this.isLoading = false;
                            });
                    }
                }
                if (searchString.length === 0) {
                    this.searchResults = [];
                    this.searchPage = [];
                    this.isLoading = false;
                }
            });
        this.nextPageUrl$
            .pipe(filter(x => x !== null, takeUntil(this.unsubscribe)))
            .subscribe(x => console.log('next page url VALUE', x));
        const zipped$ = zip(
            this.scrollDown$,
            this.nextPageUrl$.pipe(filter(x => x !== null))
        ).pipe(distinct(([_, nextPageUrl]) => nextPageUrl));
        zipped$.pipe(takeUntil(this.unsubscribe)).subscribe(([_, nextPageUrl]) => {
            console.log('handler for', nextPageUrl);
            if (this.searchString.length > 0) {
                return;
            }
            if (this.hasFullData) {
                this.users = [
                    ...this.users,
                    ...this.allUsers.slice(this.users.length, this.users.length + DISPLAY_PAGE_SIZE)
                ];
                this.nextPageUrl$.next('' + this.users.length);
                return;
            }
            console.log('request');
            const request: DirectoryRequest = { nextPageUrl, pageSize: REQUEST_PAGE_SIZE, chartId: this.chartId };
            this.directoryService.getDirectory(request).subscribe(x => {
                this.users = [...this.users, ...x.users];
                this.isLoading = false;
                if (x.nextPageUrl !== null) {
                    this.nextPageUrl$.next(x.nextPageUrl);
                }
                if (x.isFullData) {
                    if (this.users.length > DISPLAY_PAGE_SIZE) {
                        this.allUsers = this.users;
                        this.hasFullData = true;
                        this.users = this.allUsers.slice(0, DISPLAY_PAGE_SIZE);
                        if (this.users.length < this.allUsers.length) {
                            console.log('emitting new next page url');
                            this.nextPageUrl$.next('' + this.users.length);
                        }
                    } else {
                        this.isPaginationNeeded = false;
                    }
                }
                //check if container size doesn't have scroll
                setTimeout(() => {
                    if (window.innerWidth > document.documentElement.clientWidth) {
                        console.log('ENOUGH');
                    } else {
                        console.log('not enough');
                        this.scrollDown$.next();
                    }
                }, 0);
            });
        });

        this.nextPageUrl$.next('');
        this.scrollDown$.next();

        this.nodeInformation$ = this.store.select(fromChart.getInformationResults);
        this.nodeInformation$
            .pipe(
                takeUntil(this.unsubscribe),
                skip(1),
                withLatestFrom(this.store.select(fromRoot.getSharedOpenedModalNames))
            )
            .subscribe(([infoResults, openedModalNames]) => {
                if (openedModalNames && openedModalNames.length > 0) {
                    //do not open modals if one is already open
                    return;
                }
                if (infoResults && infoResults.Data && infoResults.Data.VacantPosition) {
                    this.store.dispatch(
                        new DialogShow({
                            dialogName: SharedModals.CreatePosition,
                            options: {
                                initialState: {
                                    isEditMode: false,
                                    editedPosition: infoResults.Data.VacantPosition
                                }
                            }
                        })
                    );
                } else {
                    this.store.dispatch(new DialogShow({ dialogName: SharedModals.InformationResults }));
                }
            });
    }

    onPageChanged($event: PageChangedEvent) {
        const startItem = ($event.page - 1) * $event.itemsPerPage;
        const endItem = $event.page * $event.itemsPerPage;
        this.users = this.allUsers.slice(startItem, endItem);
    }

    onPageChangedSearchResults($event: PageChangedEvent) {
        const startItem = ($event.page - 1) * $event.itemsPerPage;
        const endItem = $event.page * $event.itemsPerPage;
        this.searchPage = this.searchResults.slice(startItem, endItem);
    }

    createNextPageUrl($event: string) {
        this.nextPageUrl$.next($event);
    }

    onScrollDown() {
        this.scrollDown$.next();
    }

    onScrollUp() {
    }

    onInfo(id: string) {
        this.store.dispatch(new InfoOpen(id));
    }

    openColumns() {
        this.store.dispatch(new DialogShow({ dialogName: SharedModals.OverviewColumns }));
    }

    showLink() {
        const link = this.preloadedData.sharePointRedirectLinkForDirectory.replace('SUBSTITUTE_CHART_ID', this.chartId);
        this.store.dispatch(
            new DialogShow({ dialogName: SharedModals.ShowDirectoryLink, options: { initialState: { link } } })
        );
    }

    ngOnDestroy(): void {
        this.unsubscribe.next();
        this.unsubscribe.complete();
    }
}
