import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { DirectoryRoutingModule } from './directory-routing.module';
import { DirectoryComponent } from './components/directory.component';
import { EffectsModule } from '@ngrx/effects';
import { PaginationModule } from 'ngx-bootstrap/pagination';
import { DirectoryEffects } from './directory.effects';
import { StoreModule } from '@ngrx/store';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';
import * as fromDirectory from './directory.reducers';
import { DirectoryBoxComponent } from '../../app/shared/components/directory-box/directory-box.component';
import { FormsModule } from '@angular/forms';
import { TypeaheadModule } from 'ngx-bootstrap/typeahead';
import { BsDropdownModule } from 'ngx-bootstrap/dropdown';
import { L10nTranslationModule } from 'angular-l10n';
import { PopoverModule } from 'ngx-bootstrap/popover';
import { DirectoryService } from './directory.service';
import { InlineSVGModule } from 'ng-inline-svg';
import { SharedModule } from '../shared/shared.module';

@NgModule({
    declarations: [DirectoryComponent],
    imports: [
        CommonModule,
        FormsModule,
        TypeaheadModule,
        PopoverModule,
        L10nTranslationModule,
        DirectoryRoutingModule,
        InfiniteScrollModule,
        SharedModule,
        BsDropdownModule.forRoot(),
        InlineSVGModule.forRoot({ baseUrl: '/' }),
        PaginationModule.forRoot(),
        StoreModule.forFeature(fromDirectory.featureKey, fromDirectory.reducer),
        EffectsModule.forFeature([DirectoryEffects])
    ],
    exports : [],
    providers: [DirectoryService]
})
export class DirectoryModule {}
