import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DirectoryRequest, DirectoryResponse } from './directory.models';
import { map } from 'rxjs/operators';
import { ChartNode } from '../models';
import { Observable } from 'rxjs';

@Injectable()
export class DirectoryService {
    private API_PATH = '/api/directory/data/';
    constructor(private http: HttpClient) {}

    getDirectory(request: DirectoryRequest): Observable<DirectoryResponse> {
        return this.http.post<DirectoryResponse>(`${this.API_PATH}/${request.chartId}`, request);
    }
}
